import React, { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";

const URL =
  "https://us-central1-plantify-d36ed.cloudfunctions.net/server/registerUser";

function Register() {
  const navigation = useNavigate();
  const initialValues = {
    firstName: "",
    lastName: "",
    username: "",
    password: "",
    email: "",
  };
  const [formValues, setFormValues] = useState(initialValues);
  const [formErrors, setFormErrors] = useState({});
  const [isSubmit, setIsSubmit] = useState(false);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormValues({ ...formValues, [name]: value });
    // console.log(formValues);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setFormErrors(validate(formValues));
    setIsSubmit(true);
  };

  useEffect(() => {
    console.log(formErrors);
    if (Object.keys(formErrors).length === 0 && isSubmit) {
      handleRegister(formValues);
      console.log(formValues);
    }
  }, [formErrors]);

  const validate = (values) => {
    const errors = {};

    if (!values.firstName) {
      errors.firstName = "*First Name is blank";
    }

    if (values.lastName === "") {
      errors.lastName = "*Last Name is blank";
    }

    if (values.username === "") {
      errors.username = "*Username is blank";
    } else {
      var userCheck = /(?=.*[a-zA-Z])([a-zA-Z0-9-_]).{5,32}$/g;
      if (userCheck.test(values.username) === false) {
        errors.username = "* Username is invalid";
      }
    }

    if (values.password === "") {
      errors.password = "*Password is blank";
    } else {
      // Checks for 8-32 characters long
      // At least 1 digit, 1 Special Character
      var passCheck =
        /^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,32}$/;
      if (passCheck.test(values.password) === false) {
        errors.password = "*Password is invalid";
      }
    }

    if (values.email === "") {
      errors.email = "*Email Address is blank";
    } else {
      var emailCheck = /^[a-zA-Z0-9+_.-]+@[a-zA-Z0-9.-]+\.[a-zA-z]+$/g;
      if (emailCheck.test(values.email) === false) {
        errors.email = "*Email Address is not valid";
      }
    }

    return errors;
  };

  const handleRegister = (values) => {
    // console.log(firstName + " " + lastName + " " + username + " " + password + " " + email);
    fetch(URL, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        firstName: values.firstName,
        lastName: values.lastName,
        email: values.email,
        username: values.username,
        password: values.password,
      }),
    })
      .then((data) => {
        console.log(data);
        navigation("/pages/home.js");
      })
      .catch((error) => console.error(error));
  };

  return (
    <div className="register-page">
      {/* <pre>{JSON.stringify(formValues, undefined, 2)}</pre> */}
      <div className="register-container">
        <div className="container-half">
          <div classname="register-header">
            <h1 id="plantify">Plantify.</h1>
            <h2>Create an account to get started using Plantify</h2>
            <hr />
          </div>

          <div
            id="register-input"
            className="input-group"
          >
            <label htmlFor="First-Name">First Name</label>
            <input
              type="text"
              name="firstName"
              id="register-first-name"
              value={formValues.firstName}
              // onChange={(e) => setFirstName(e.target.value)}
              onChange={handleChange}
            />
            <p className="error">{formErrors.firstName}</p>

            <label htmlFor="Last-Name">Last Name</label>
            <input
              type="text"
              name="lastName"
              id="register-last-name"
              value={formValues.lastName}
              // onChange={(e) => setLastName(e.target.value)}
              onChange={handleChange}
            />
            <p className="error">{formErrors.lastName}</p>
          </div>
        </div>

        <div className="container-half">
          <div
            id="register-input"
            className="input-group"
          >
            <label htmlFor="Email">Email</label>
            <input
              type="email"
              name="email"
              id="register-email"
              value={formValues.email}
              // onChange={(e) => setEmail(e.target.value)}
              onChange={handleChange}
            />
            <p className="error">{formErrors.email}</p>

            <label htmlFor="Username">Username</label>
            <input
              type="text"
              name="username"
              id="register-username"
              value={formValues.username}
              // onChange={(e) => setUsername(e.target.value)}
              onChange={handleChange}
            />
            <p className="error">{formErrors.username}</p>

            <label htmlFor="Password">Password</label>
            <input
              type="password"
              name="password"
              id="register-password"
              value={formValues.password}
              // onChange={(e) => setPassword(e.target.value)}
              onChange={handleChange}
            />
            <p className="error">{formErrors.password}</p>
          </div>
        </div>

        <Link to="/pages/authDetails.js">
          <input
            id="register-button"
            type="button"
            value="Register"
            onClick={handleSubmit}
          />
        </Link>
        <Link
          className="login-register-swap"
          to="/pages/login.js"
        >
          <span>
            <br></br>Already have an account?
          </span>
        </Link>
      </div>
    </div>
  );
}

export default Register;
